
<template>
  <div class="cms-list-layout" >
    <section class="cms-batch">
          <cms-button icon="success" type="success" size="small" @click="markreads(ids)">标记为已读</cms-button>
          <cms-button icon="error" type="error" size="small" @click="deleteDatas()">批量删除</cms-button>
        </section>
    <el-table v-loading="loading" :data="tableData" style="width: 100%" stripe  @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="messageId" label="id" width="60"></el-table-column>
      <el-table-column prop="" label="内容" min-width="200">
        <template slot-scope="scope">
          <div class="text-overflow" :class="scope.row.status?'t-label':''">
            <span v-if="!scope.row.status" class="color" :style="'color:'+color">•</span>
            <span @click.stop="$routerLink('/station/view','view',{id:scope.row.messageId})">{{scope.row.content}}</span>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="createTime" label="发送时间" min-width="170">
        <template  slot-scope="scope">
           {{$moment(scope.row.createTime)}}
        </template>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import { storeMessageList, storeMessageDelete, storeMessageSave } from '@/api/index/index.js'
var defaultData = {
  'page': 1,
  'size': 10 }
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  computed: {
    ids() { // 选中的id 数组 仅属性为id可用
      const arr = []
      this.checkItems.map(v => {
        arr.push(v.messageId)
      })
      return arr
    },
    color() {
      return this.$store.state.system.color
    }
  },
  methods: {
    // 批量标记为已读
    markreads(ids, message = '确定全部变更为已读状态?') {
      if (this.batchStatus) {
        this.$confirm(message, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          storeMessageSave({ ids: ids }).then(res => {
            if (res.code === 200) {
              this.$successMessage('全部已阅')
              this.$store.dispatch('setInfoData')
              this.getTableData(this.callback, this.queryParams, this.isPage)
            } else {
              this.$errorMessage('操作失败,' + res.message)
            }
          })
        })
          .catch(() => {})
      } else {
        this.$errorMessage('至少选择一条消息')
      }
    },
    deleteDatas() {
      this.deleteTableDatas(storeMessageDelete, this.ids) // 这个才是真实的删除方法
    }
  },
  created() {
    // 首次触发
    this.getTableData(storeMessageList, this.queryParams)
  }
}
</script>
<style>
.t-label{
  color: #999;
}
.color{
  display: inline-block;
}
</style>



